import React, { ChangeEvent, useState } from 'react'
import { useCallback } from 'react'
import { getIncomers, Handle, NodeProps, Position, useReactFlow, useUpdateNodeInternals } from 'reactflow'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { NodeProcess, resolve } from '@/components/flow/nodeTypes'
import { ALPHABETS, NodeCard, useNodeDataState } from '@/components/flow/nodes/base'
import { Button } from '@/components/ui/button'

export type CaesarData = {
  shift?: number;
  input?: string;
};

export const CaesarProcess: NodeProcess<CaesarData> = (node, params) => {
  const incomers = getIncomers(
    node,
    params.nodes,
    params.edges,
  )
  if (incomers[0]) {
    const text = resolve(incomers[0], params)
    if (node.data.input != text) {
      params.updateNodeData(node.id, {
        input: text,
      })
    }
    const shifted = Shift(ALPHABETS, node.data.shift ?? 0)
    return text.replace(/[a-z]/gi, letter => shifted[ALPHABETS.indexOf(letter)])
  }
  return ''
}

export function Caesar({ id, data: initialData }: NodeProps<CaesarData>) {
  const [data, setData] = useNodeDataState<CaesarData>(id, initialData)
  const [highlightIndex, setHighlightIndex] = useState<number|undefined>()
  const shiftedAlphabetsText = useCallback(() => {
    return Shift(ALPHABETS, data.shift ?? 0)
  }, [data])

  React.useEffect(() => {
    if (data.input) {
      const needle = data.input?.slice(-1)
      setHighlightIndex(shiftedAlphabetsText().indexOf(needle))
    } else {
      setHighlightIndex(undefined)
    }
  }, [data])

  const shiftLeft = () => {
    setData({
      shift: (data.shift ?? 0) - 1,
    })
  }
  const shiftRight = () => {
    setData({
      shift: (data.shift ?? 0) + 1,
    })
  }

  return (
    <NodeCard label="Caesar cipher">
      <div className={'flex flex-col m-auto gap-2'}>
        <div className={'flex flex-row gap-2'}>
          <Button size={'xs'} onClick={shiftLeft} className={'flex-0 w-12'} variant="outline"></Button>
          <div className={'flex-1 text-center text-muted-foreground text-xs m-auto'}>
            ROTATE: {data.shift}
          </div>
          <Button size={'xs'} onClick={shiftRight} className={'flex-0 w-12'} variant="outline"></Button>
        </div>
        <Label className={'my-auto font-mono'}><Highlight index={highlightIndex} text={shiftedAlphabetsText()} /></Label>
        <Label className={'my-auto font-mono'}><Highlight index={highlightIndex} text={ALPHABETS} /></Label>
      </div>
    </NodeCard>
  )
}

export function Shift(text: string, shift: number) {
  return text.slice(-shift) + text.slice(0, -shift)
}

function Highlight({ index, text }: { index?: number, text: string }) {
  return text.split('').map((s, k) => {
    if (index != undefined && (k) === index) {
      return <span key={k} className={'text-red-400'}>{s}</span>
    }
    return s
  })
}