import React from 'react'
import { NodeProps, Node, Edge, getIncomers } from 'reactflow'
import { Suffix, SuffixProcess } from '@/components/flow/nodes/suffix'
import { Prefix, PrefixProcess } from '@/components/flow/nodes/prefix'
import { Caesar, CaesarProcess } from '@/components/flow/nodes/caesar'
import { Vigenere, VigenereProcess } from '@/components/flow/nodes/vigenere'
import { Output, OutputProcess } from '@/components/flow/nodes/output'
import { CharacterCounter, CharacterCounterProcess } from '@/components/flow/nodes/character-counter'
import { WordCounter, WordCounterProcess } from '@/components/flow/nodes/word-counter'
import { Input, InputProcess } from '@/components/flow/nodes/input'

type ResolveBaseParams = {
  updateNodeData: (nodeId: string, newData: any) => void
  nodes: Node[],
  edges: Edge[],
}

export const TerminalNodeTypes = [
  'output',
  'character_counter',
  'word_counter',
]

type NodeType = (node: NodeProps<any>) => React.JSX.Element
export type NodeProcess<T> = (node: Node<T>, params: ResolveBaseParams) => string
export const NodeTypes: {[type in string]: {
  nodeType: NodeType,
  process: NodeProcess<any>,
  defaultData: Object
}} = {
  input: {
    nodeType: Input,
    process: InputProcess,
    defaultData: {
      value: ''
    }
  },
  output: {
    nodeType: Output,
    process: OutputProcess,
    defaultData: {
      value: ''
    }
  },
  suffix: {
    nodeType: Suffix,
    process: SuffixProcess,
    defaultData: {
      suffix: '.png'
    }
  },
  prefix: {
    nodeType: Prefix,
    process: PrefixProcess,
    defaultData: {
      prefix: 'pre_'
    }
  },
  caesar: {
    nodeType: Caesar,
    process: CaesarProcess,
    defaultData: {
      shift: 3
    }
  },
  vigenere_encrypt: {
    nodeType: Vigenere,
    process: VigenereProcess,
    defaultData: {
      key: 'ARM'
    }
  },
  character_counter: {
    nodeType: CharacterCounter,
    process: CharacterCounterProcess,
    defaultData: {
      value: ''
    }
  },
  word_counter: {
    nodeType: WordCounter,
    process: WordCounterProcess,
    defaultData: {
      value: ''
    }
  }
}

export function useNodeTypes() {
  return React.useMemo(() => {
    const results: {[key in string]: NodeType} = {}
    for (let nodeType in NodeTypes) {
      results[nodeType] = NodeTypes[nodeType].nodeType
    }
    return results
  }, []);
}

export const resolve = (node: Node, params: ResolveBaseParams): string => {
  if (node.type && NodeTypes[node.type]) {
    return NodeTypes[node.type].process(node, params)
  }
  return ''
}
