import React, { ChangeEvent, useState } from 'react'
import { useCallback } from 'react';
import { getIncomers, Handle, NodeProps, Position, useReactFlow, useUpdateNodeInternals } from 'reactflow'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { NodeProcess, resolve } from '@/components/flow/nodeTypes'
import { NodeCard, useNodeDataState } from '@/components/flow/nodes/base'

export type SuffixData = {
  suffix?: string;
};

export const SuffixProcess: NodeProcess<SuffixData> = (node, params) => {
  const suffix = node.data.suffix ?? ''
  const incomers = getIncomers(
    node,
    params.nodes,
    params.edges,
  );
  if (incomers[0]) {
    return resolve(incomers[0], params) + suffix
  }
  return '' + suffix
}

export function Suffix({ id, data: initialData }: NodeProps<SuffixData>) {
  const [data, setData] = useNodeDataState<SuffixData>(id, initialData);

  const onChange = useCallback((e: ChangeEvent<HTMLInputElement>) => {
    setData({ suffix: e.target.value })
  }, []);

  return (
    <NodeCard label="Suffix">
      <div className={'flex m-auto gap-2'}>
        <Label htmlFor="text" className={'my-auto'}>+</Label>
        <Input id="text" name="text" value={data.suffix ?? ''} onChange={onChange} className="nodrag" />
      </div>
    </NodeCard>
  );
}