import React, { ChangeEvent, useState } from 'react'
import { useCallback } from 'react';
import { getIncomers, Handle, NodeProps, Position, useReactFlow, useUpdateNodeInternals } from 'reactflow'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { NodeProcess, resolve } from '@/components/flow/nodeTypes'
import { NodeCard, useNodeDataState } from '@/components/flow/nodes/base'

export type CharacterCounterData = {
  value?: string;
};

export const CharacterCounterProcess: NodeProcess<CharacterCounterData> = (node, params) => {
  const incomers = getIncomers(
    node,
    params.nodes,
    params.edges,
  );
  if (incomers[0]) {
    params.updateNodeData(node.id, {
      value: resolve(incomers[0], params),
    })
    return resolve(incomers[0], params)
  }
  params.updateNodeData(node.id, {
    value: '',
  })
  return ''
}

type Counts = {
  character: string,
  count: number,
  percentage: string,
}[]

export function CharacterCounter({ id, data: initialData }: NodeProps<CharacterCounterData>) {
  const [data, setData] = useNodeDataState<CharacterCounterData>(id, initialData);
  const [counts, setCounts] = useState<Counts>([])

  React.useEffect(() => {
    if (!data.value) {
      setCounts([])
      return
    }
    const allCharCount = data.value.length
    const counts: Counts = []
    for (let char of data.value.split('')) {
      const index = counts.findIndex((c) => c.character === char)
      if (index == -1) {
        counts.push({
          character: char,
          count: 1,
          percentage: '0',
        })
      } else {
        counts[index] = {
          character: char,
          count: counts[index].count + 1,
          percentage: '0',
        }
      }
    }
    for (let count of counts) {
      count.percentage = `${ ((count.count / allCharCount) * 100).toFixed(2) }%`
    }

    counts.sort((a, b) => {
      return a.count > b.count ? -1 : 1
    })
    setCounts(counts)
  }, [data])

  return (
    <div className="px-4 py-2 shadow-md rounded-md bg-white border-2 border-stone-400">
      <div className={'pb-2'}>
        <Label className={'text-left text-muted-foreground'}>Character counter (Top 10)</Label>
      </div>
      <div className={'flex flex-col justify-center m-auto gap-2 font-mono whitespace-pre'}>
        {counts.map((c, k) => {
          if (k > 10) {
            return
          }
          return <div key={c.character}>
            {c.character}: {c.count} ({c.percentage}%) <br />
          </div>
        })}
      </div>
      <Handle type="target" position={Position.Top} className="" />
    </div>
  )
}