import React, { ChangeEvent, useState } from 'react'
import { useCallback } from 'react';
import { getIncomers, Handle, NodeProps, Position, useReactFlow, useUpdateNodeInternals } from 'reactflow'
import { Input as InputField } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { NodeProcess, resolve } from '@/components/flow/nodeTypes'
import { NodeCard, useNodeDataState } from '@/components/flow/nodes/base'
import { Textarea } from '@/components/ui/textarea'

export type InputData = {
  value?: string;
};

export const InputProcess: NodeProcess<InputData> = (node, params) => {
  return node.data.value ?? ''
}

export function Input({ id, data: initialData }: NodeProps<InputData>) {
  const [data, setData] = useNodeDataState<InputData>(id, initialData);

  const onChange = useCallback((e: ChangeEvent<HTMLTextAreaElement>) => {
    // ALPHABET + SPACE ONLY
    const result = e.target.value.toUpperCase().replace(/[^A-Z| ]/g, '');
    setData({ value: result })
  }, []);

  return (
    <div className="">
      <div className={'pb-2'}>
        <Label className={'text-left text-muted-foreground'}>Input</Label>
      </div>
      <div className={'flex justify-center m-auto gap-2'}>
        <Textarea id="text" name="text" value={data.value ?? ''} onChange={onChange} className="nodrag" />
      </div>
      <Handle type="source" position={Position.Bottom} className="" />
    </div>
    )
}