import React, { ChangeEvent, PropsWithChildren, useState } from 'react'
import { Handle, Position, useNodes, useReactFlow, useUpdateNodeInternals } from 'reactflow'
import { Label } from '@/components/ui/label'

export const ALPHABETS ='ABCDEFGHIJKLMNOPQRSTUVWXYZ'

export function useNodeDataState<T>(id: string, initialData: T): [T, React.Dispatch<T>] {
  const [data, setRawData] = useState(initialData);

  const nodes = useNodes();
  const reactFlow = useReactFlow();

  const setData = (newData: T) => {
    reactFlow.setNodes((nds) =>
      nds.map((node) =>
        node.id === id ? { ...node, data: { ...node.data, ...newData } } : node
      )
    );
  };

  React.useEffect(() => {
    if (reactFlow.getNode(id)?.data != data) {
      setRawData(reactFlow.getNode(id)?.data)
    }
  }, [nodes])

  return [data, setData]
}

export function NodeCard({ label, children }: {label: string} & PropsWithChildren) {
  return (
    <div className="px-4 py-2 shadow-md rounded-md bg-white border-2 border-stone-400">
      <div className={'pb-2'}>
      <Label className={'text-muted-foreground'}>{label}</Label>
      </div>
      {children}
      <Handle type="target" position={Position.Top} className="" />
      <Handle type="source" position={Position.Bottom} className="" />
    </div>
  );
}