import React, { ChangeEvent, useState } from 'react'
import { useCallback } from 'react';
import { getIncomers, Handle, NodeProps, Position, useReactFlow, useUpdateNodeInternals } from 'reactflow'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { NodeProcess, resolve } from '@/components/flow/nodeTypes'
import { NodeCard, useNodeDataState } from '@/components/flow/nodes/base'

export type OutputData = {
  value?: string;
};

export const OutputProcess: NodeProcess<OutputData> = (node, params) => {
  const incomers = getIncomers(
    node,
    params.nodes,
    params.edges,
  );
  if (incomers[0]) {
    params.updateNodeData(node.id, {
      value: resolve(incomers[0], params),
    })
    return resolve(incomers[0], params)
  }
  params.updateNodeData(node.id, {
    value: '',
  })
  return ''
}

export function Output({ id, data: initialData }: NodeProps<OutputData>) {
  const [data, setData] = useNodeDataState<OutputData>(id, initialData);

  return (
    <div className="">
      <div className={'pb-2'}>
        <Label className={'text-left text-muted-foreground'}>Output</Label>
      </div>
      <div className={'flex justify-center m-auto gap-2'}>
        <Label className={'text-2xl'}>{data.value}</Label>
      </div>
      <Handle type="target" position={Position.Top} className="" />
    </div>
    )
}