import {
  Node,
  Edge,
  applyEdgeChanges,
  applyNodeChanges,
  Background,
  EdgeChange,
  NodeChange,
  ReactFlow,
  addEdge,
  Connection, Controls,
  BackgroundVariant, Panel, getIncomers, getOutgoers, useReactFlow, ReactFlowInstance,
} from 'reactflow'
import * as React from 'react'

import 'reactflow/dist/style.css'
import { useCallback } from 'react'
import { resolve, NodeTypes, useNodeTypes, TerminalNodeTypes } from '@/components/flow/nodeTypes'
import { nanoid } from 'nanoid'

const INPUT_NODE = {
  id: '1',
  type: 'input',
  data: { value: 'THE QUICK BROWN FOX' },
  position: { x: 250, y: 0 },
}
const OUTPUT_NODE = {
  id: '2',
  type: 'output',
  data: { label: 'Output Node' },
  position: { x: 250, y: 800 },
}
const initialNodes: Node<any>[] = [
  INPUT_NODE,
  OUTPUT_NODE,
  {
    id: '3',
    type:'caesar',
    data: { shift: 3 },
    position: { x: 100, y: 350 },
  },
  {
    id: '4',
    type:'caesar',
    data: { shift: -3 },
    position: { x: 100, y: 550 },
  },
  {
    id: '5',
    type:'vigenere_encrypt',
    data: { key: 'KEY' },
    position: { x: 500, y: 350 },
  },
  {
    id: '6',
    type:'output',
    data: { },
    position: { x: 500, y: 1000 },
  },
]

const initialEdges: Edge<any>[] = [
  { id: 'e1-2', source: INPUT_NODE.id, target: '3' },
  { id: 'e2-3', source: '3', target: '4' },
  { id: 'e2-4', source: '4', target: OUTPUT_NODE.id },
  { id: 'e2-5', source: INPUT_NODE.id, target: '5' },
  { id: 'e2-6', source: '5', target: '6' },
]

export function Flow() {

  const [nodes, setNodes] = React.useState(initialNodes)
  const [edges, setEdges] = React.useState(initialEdges)
  const [reactFlowInstance, setReactFlowInstance] = React.useState<ReactFlowInstance>();

  const nodeTypes = useNodeTypes()

  React.useEffect(() => {
    for (let node of nodes) {
      if (node.type && TerminalNodeTypes.includes(node.type)) {
        resolve(node, {
          nodes,
          edges,
          updateNodeData: (nodeId, newData) => {
            setNodes((nds) =>
              nds.map((node) =>
                node.id === nodeId ? { ...node, data: { ...node.data, ...newData } } : node
              )
            );
          }
        })
      }
    }
  }, [nodes, edges])

  const onNodesChange = useCallback(
    (changes: NodeChange[]) => setNodes((nds) => applyNodeChanges(changes, nds)),
    [setNodes],
  )
  const onEdgesChange = useCallback(
    (changes: EdgeChange[]) => setEdges((eds) => applyEdgeChanges(changes, eds)),
    [setEdges],
  )

  // cycle validation
  const isValidConnection = useCallback(
    (connection: { target: any; source: any }) => {
      const target = nodes.find((node: { id: any }) => node.id === connection.target);
      const hasCycle = (node: Node, visited = new Set()) => {
        if (visited.has(node.id)) return false;

        visited.add(node.id);

        for (const outgoer of getOutgoers(node, nodes, edges)) {
          if (outgoer.id === connection.source) return true;
          if (hasCycle(outgoer, visited)) return true;
        }
      };

      if (target) {
        if (target.id === connection.source) return false;
        return !hasCycle(target);
      }
      return false
    },
    [nodes, edges],
  );

  const onConnect = useCallback(
    (connection: Edge | Connection) => setEdges((eds) => addEdge(connection, eds)),
    [setEdges]
  );

  const onDragOver: React.DragEventHandler<HTMLDivElement> = useCallback((event) => {
    event.preventDefault();
    event.dataTransfer.dropEffect = 'move';
  }, []);

  const onDrop:  React.DragEventHandler<HTMLDivElement> = useCallback(
    (event) => {
      event.preventDefault();

      const type = event.dataTransfer.getData('application/reactflow');

      // check if the dropped element is valid
      if (typeof type === 'undefined' || !type) {
        return;
      }

      // reactFlowInstance.project was renamed to reactFlowInstance.screenToFlowPosition
      // and you don't need to subtract the reactFlowBounds.left/top anymore
      // details: https://reactflow.dev/whats-new/2023-11-10
      // @ts-ignore
      const position = reactFlowInstance.screenToFlowPosition({
        x: event.clientX,
        y: event.clientY,
      });
      const newNode = {
        id: nanoid(),
        type,
        position,
        data: NodeTypes[type].defaultData ?? {},
      };

      setNodes((nds) => nds.concat(newNode));
    },
    [reactFlowInstance],
  );

  return (
    <ReactFlow nodes={nodes}
               edges={edges}
               onNodesChange={onNodesChange}
               onEdgesChange={onEdgesChange}
               onConnect={onConnect}
               nodeTypes={nodeTypes}
               isValidConnection={isValidConnection}
               onInit={setReactFlowInstance}
               onDrop={onDrop}
               onDragOver={onDragOver}
               fitView >
      <Background color="#ccc" variant={BackgroundVariant.Cross} />
      <Controls />
      <Panel position="top-left">
        <h1 className={'text-2xl font-bold font-mono text-muted-foreground'}>VISUALCIPHER</h1>
      </Panel>
      <Panel position="bottom-right">
        <Palette/>
      </Panel>
    </ReactFlow>
  )
}

function Palette() {
  const onDragStart = (event: React.DragEvent, nodeType: string) => {
    event.dataTransfer.setData('application/reactflow', nodeType);
    event.dataTransfer.effectAllowed = 'move';
  };

  return <div className={'flex flex-col gap-2'}>
    <div className="description"></div>
    {Object.keys(NodeTypes).map((key) => (
      <div key={key} className="w-full bg-background border-l-8 border-2 px-2" onDragStart={(event) => onDragStart(event, key)} draggable>
        {key}
      </div>
    ))}
  </div>
}