import React, { ChangeEvent, useState } from 'react'
import { useCallback } from 'react';
import { getIncomers, Handle, NodeProps, Position, useReactFlow, useUpdateNodeInternals } from 'reactflow'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { NodeProcess, resolve } from '@/components/flow/nodeTypes'
import { NodeCard, useNodeDataState } from '@/components/flow/nodes/base'

export type PrefixData = {
  prefix?: string;
};

export const PrefixProcess: NodeProcess<PrefixData> = (node, params) => {
  const prefix = node.data.prefix ?? ''
  const incomers = getIncomers(
    node,
    params.nodes,
    params.edges,
  );
  if (incomers[0]) {
    return prefix + resolve(incomers[0], params)
  }
  return prefix + ''
}

export function Prefix({ id, data: initialData }: NodeProps<PrefixData>) {
  const [data, setData] = useNodeDataState<PrefixData>(id, initialData);

  const onChange = useCallback((e: ChangeEvent<HTMLInputElement>) => {
    setData({ prefix: e.target.value })
  }, []);

  return (
    <NodeCard label="Prefix">
      <div className={'flex m-auto gap-2'}>
        <Input id="text" name="text" value={data.prefix ?? ''} onChange={onChange} className="nodrag" />
        <Label htmlFor="text" className={'my-auto'}>+</Label>
      </div>
    </NodeCard>
  );
}