import React, { ChangeEvent, useState } from 'react'
import { useCallback } from 'react'
import { getIncomers, Handle, NodeProps, Position, useReactFlow, useUpdateNodeInternals } from 'reactflow'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { NodeProcess, resolve } from '@/components/flow/nodeTypes'
import { ALPHABETS, NodeCard, useNodeDataState } from '@/components/flow/nodes/base'
import { Button } from '@/components/ui/button'
import { Table, TableBody, TableCaption, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table'
import { Shift } from '@/components/flow/nodes/caesar'

export type VigenereData = {
  key?: string;
  input?: string;
};

export const VigenereProcess: NodeProcess<VigenereData> = (node, params) => {
  const incomers = getIncomers(
    node,
    params.nodes,
    params.edges,
  )
  if (incomers[0]) {
    const text = resolve(incomers[0], params)
    if (node.data.input != text) {
      params.updateNodeData(node.id, {
        input: text,
      })
    }
    const result = VigenereEncrypt(text, node.data.key ?? '')
    return result.encrypted
  }
  if (node.data.input != '') {
    params.updateNodeData(node.id, {
      input: '',
    })
  }
  return ''
}

export function VigenereEncrypt(text: string, key: string): {
  encrypted: string,
  lastKeyCharacter: string,
  lastTextCharacter: string,
  lastEncryptedCharacter: string,
} {
  if (!key) {
    return {
      encrypted: text,
      lastKeyCharacter: '',
      lastTextCharacter: '',
      lastEncryptedCharacter: '',
    }
  }
  let encrypted = ''
  let keyCharacter = ''
  let textCharacter = ''
  let encryptedCharacter = ''
  for (textCharacter of text.split('')) {
    keyCharacter = key.split('')[encrypted.length % key.length]
    const keyIndex = ALPHABETS.indexOf(keyCharacter)
    const textIndex = ALPHABETS.indexOf(textCharacter)
    if (textIndex == -1 ){
      encryptedCharacter = ' '
    } else {
      const encrypts = Shift(ALPHABETS, -keyIndex)
      encryptedCharacter = encrypts.slice(textIndex, textIndex + 1)
    }
    encrypted += encryptedCharacter
  }
  return {
    encrypted: encrypted,
    lastKeyCharacter: keyCharacter,
    lastTextCharacter: textCharacter,
    lastEncryptedCharacter: encryptedCharacter,
  }
}

export function Vigenere({ id, data: initialData }: NodeProps<VigenereData>) {
  const [data, setData] = useNodeDataState<VigenereData>(id, initialData)
  const [highlightKey, setHighlightKey] = useState('')
  const [highlightText, setHighlightText] = useState('')
  const [highlightEncrypted, setHighlightEncrypted] = useState('')

  const onChange = useCallback((e: ChangeEvent<HTMLInputElement>) => {
    // ALPHABET ONLY
    const result = e.target.value.toUpperCase().replace(/[^A-Z]/g, '');
    setData({ key: result })
  }, [])

  React.useEffect(() => {
    console.log(data.input)
    if (data.input) {
      const result = VigenereEncrypt(data.input, data.key ?? '')
      setHighlightKey(result.lastKeyCharacter)
      setHighlightText(result.lastTextCharacter)
      setHighlightEncrypted(result.lastEncryptedCharacter)
    } else {
      setHighlightKey('')
      setHighlightText('')
      setHighlightEncrypted('')
    }
  }, [data])

  return (
    <NodeCard label="Vigenere cipher (encrypt)">
      <div className={'flex flex-col m-auto gap-2'}>
        <div className={'flex flex-row gap-2'}>
          <div className={'flex-1 text-center text-muted-foreground text-xs m-auto'}>
            KEY
          </div>
          <Input id="text" name="text" value={data.key ?? ''} onChange={onChange} className="nodrag font-mono" />
        </div>
        <div className={'pt-4 px-4 font-mono whitespace-pre leading-4 tracking-widest'}>
          {'  '}{ALPHABETS.split('').map((s) => {
          if (s === highlightText) {
            return <span key={s} className={'text-red-400'}>{s}</span>
          }
          return <span key={s}>{s}</span>
        })} <br />
          {'  --------------------------'}<br />
          {ALPHABETS.split('').map((s, k) => {
            return <div key={k}>{s === highlightKey ?
              <span className={'text-red-400'}>{s}</span> : s
            }
            |
              {Shift(ALPHABETS, -k).split('').map((e) => {
                if (s === highlightKey) {
                  if (e===highlightEncrypted) {
                    return <span key={e} className={'text-red-400'}>{e}</span>
                  }
                  return <span key={e} className={'text-slate-400'}>{e}</span>
                }
                return <span key={e}>{e}</span>
              })}<br /></div>
          })}
        </div>
        <div className={'text-muted-foreground mx-auto'}>The Vigenre square</div>
      </div>
    </NodeCard>
  )
}

function Highlight({ index, text }: { index?: number, text: string }) {
  return text.split('').map((s, k) => {
    if (index != undefined && (k) === index) {
      return <span key={k} className={'text-red-400'}>{s}</span>
    }
    return s
  })
}